import 'regenerator-runtime';
import * as d3 from 'd3';
import utils from '/app/components/utils';
import './squares.scss';

const squares = (data, options) => {
  const defaultConfig = {
    distance: 2,
    fill: null,
    height: 0,
    id: null,
    position: 'centered',
    ratio: 10,
    shape: 'square',
    size: 2,
    stroke: null,
    strokeWidth: 1,
    target: null,
    targetObject: null,
    width: 0,
  };
  
  const init = () => {
    const container = config.targetObject.append('div')
      .attr('class', 'pointCharts')
      .attr('id', config.id);
    
    const cnvs = container.append('canvas')
      .attr('id', `canavs-${config.id}`)
      .attr('width', config.width)
      .attr('height', config.height);
    
    const ctx = cnvs.node().getContext('2d');

    const shapes = Math.round(data / config.ratio);
    const shapesPerLine = Math.round(Math.sqrt(shapes));
    
    let offsetX = 1;
    let offsetY = 1;
    switch (config.position) {
      case 'centered':
        offsetX = (Math.floor(config.width / 2)) - Math.round(((config.size + config.distance) * shapesPerLine) / 2);
        offsetY = (Math.floor(config.height / 2)) - Math.round(((config.size + config.distance) * Math.ceil(shapes / shapesPerLine)) / 2);
        break;
      case 'bottom-right':
        offsetX = config.width - ((config.size + config.distance) * shapesPerLine);
        offsetY = config.height - ((config.size + config.distance) * Math.ceil(shapes / shapesPerLine));
        break;
      case 'top-right':
        offsetX = config.width - ((config.size + config.distance) * shapesPerLine);
        offsetY = 1;
        break;
      case 'bottom-left':
        offsetX = 1;
        offsetY = config.height - ((config.size + config.distance) * Math.ceil(shapes / shapesPerLine));
        break;
      case 'top-center':
        offsetX = (Math.floor(config.width / 2)) - Math.round(((config.size + config.distance) * shapesPerLine) / 2);
        offsetY = 1;
        break;
      default:
        offsetX = 1;
        offsetY = 1;
        break;
    }

    ctx.fillStyle = config.fill;
    ctx.strokeStyle = config.stroke;
    ctx.strokeWidth = config.strokeWidth;

    for (let shape = 0; shape < shapes; shape++) {
      if (config.shape === 'circle') {
        const xPos = offsetX + (shape % shapesPerLine) * (config.distance + config.size) + Math.floor(config.size / 2);
        const yPos = offsetY + Math.floor(shape / shapesPerLine) * (config.distance + config.size) + Math.floor(config.size / 2);
        const radius = (config.stroke) ? Math.floor((config.size / 2) - config.strokeWidth / 2) : Math.floor(config.size / 2);
        ctx.beginPath();
        ctx.arc(xPos, yPos, radius, 0, 2 * Math.PI);
        if (config.fill) {
          ctx.fill();
        }
        if (config.stroke) {
          ctx.stroke();
        }
      } else {
        const xPos = offsetX + (shape % shapesPerLine) * (config.distance + config.size);
        const yPos = offsetY + Math.floor(shape / shapesPerLine) * (config.distance + config.size);
        if (config.fill) {
          ctx.fillRect(xPos, yPos, config.size, config.size);
        }
        if (config.stroke) {
          ctx.strokeRect(xPos + Math.round(config.strokeWidth / 2), yPos + Math.round(config.strokeWidth / 2), config.size - config.strokeWidth, config.size - config.strokeWidth);
        }
      }
    }
  }
  
  let config = utils.merge(defaultConfig, options);
  config.targetObject = d3.select(config.target);  
  if (
    config.id
    && config.target
    && !config.targetObject.empty()
    && config.height > 0
    && config.width > 0
  ) {
    init();
  } else {
    console.error('Squares failed initialization');
  }
};

export default squares;